$('.catalog__slider').slick({
    slidesToShow: 6,
    slidesToScroll: 1,
    variableWidth: true,
    infinite: false,
    prevArrow: $('.catalog__prev'),
    nextArrow: $('.catalog__next'),
    responsive: [
        {
            breakpoint: 1024,
            settings: {
                slidesToShow: 5,
            }
        },
        {
            breakpoint: 651,
            settings: {
                slidesToShow: 4,
            }
        },
        {
            breakpoint: 577,
            settings: {
                slidesToShow: 3,
            }
        },
    ]
});


$(document).ready(function() {

//Menu
    $('.burger').click(function () {
        $(this).toggleClass('burger--active')
        $('.header-top__right').toggleClass('header-top__right--active')
        $('body').toggleClass('fixed-body')
    })

// Search
    $(".search-show input").on("input", function() {
        let val = $(this).val()

        if (val.length > 2) {
            $(".search-result").addClass('search-result--active')
        } else {
            $(".search-result").removeClass('search-result--active')
        }
    })

    $('.search-btn__mob').click(function () {
        $('.search-mob').addClass('search-mob--active')
        $('body').addClass('fixed-body')
    })

    $('.search-close').click(function () {
        $('.search-mob').removeClass('search-mob--active')
        $('body').removeClass('fixed-body')
    })

    // Tabs
    $(".tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab").removeClass("tab--active")
        $(this).addClass("tab--active")

        $(this).parents(".tabs-wrap").find(".tabs__content").removeClass("tabs__content--active")
        $(this).parents(".tabs-wrap").find(`.tabs__content[data-price-path="${path}"]`).addClass("tabs__content--active")
    })

    $(".tab2").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".tab2").removeClass("tab--active")
        $(this).addClass("tab2--active")

        $(this).parents(".catalog__tabs").find(".catalog__content").removeClass("catalog__content--active")
        $(this).parents(".catalog__tabs").find(`.catalog__content[data-price-path="${path}"]`).addClass("catalog__content--active")
    })

    $('.catalog__slider .slick-slide').click(function () {
        $(this).siblings('.slick-slide').find('.tab2').removeClass('tab2--active')
        console.log(111)
    })

    //Next - Prev btn
    $('.catalog__next').click(function () {
        $('.catalog__prev').addClass('catalog__prev--active')
    })

    // Select
    $(".custom_select select").niceSelect()

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').addClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $(this).parents('.select__wrap').find('.select__current span').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).click(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

})

// Animation
const animItems = document.querySelectorAll('._anim-items')

if (animItems.length > 0) {
    window.addEventListener('scroll', animOnScroll)
    function animOnScroll(params) {
        for (let index = 0; index < animItems.length; index++) {
            const animItem = animItems[index];
            const animItemHeight = animItem.offsetHeight;
            const animItemOffset = offset(animItem).top;
            const animStart = 4;

            let animItemPoint = window.innerHeight - animItemHeight / animStart;

            if (animItemHeight > window.innerHeight) {
                animItemPoint = window.innerHeight - window.innerHeight / animStart;
            }

            if ((pageYOffset > animItemOffset - animItemPoint) && pageYOffset < (animItemOffset + animItemHeight)) {
                animItem.classList.add('active');
            } else {
                if (!animItem.classList.contains('anim-no-hide')) {
                    animItem.classList.remove('active')
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return { top: rect.top + scrollTop, left: rect.left + scrollLeft }
    }

    setTimeout(() => {
        animOnScroll();
    }, 300)
}
